<script>
import { mapState } from 'vuex'
import { getShopGeneral } from '@/api/shop/shopIndex'
import { platformlogin } from '@/api/administrator'
import { setToken } from '@/utils/auth'
export default {
  data(){
    return{
      form:{}
    }
  },
  created(){
  },
  mounted() {
    this.getData()
  },
  methods:{
    getData(){
      let userId = localStorage.getItem('userId')
      console.log(userId, 'userId')
      if (!userId) return
      getShopGeneral(this.shop.shopId, userId).then(res=>{
        if(res.code == 200){
          this.form = res.data
          sessionStorage.setItem('roleName', res.data.roleName)
        }else{
          this.$message.error(res.message);
        }
      })
    },
    jumps(l,obj){
      this.$store.commit('SET_MENU_ACTIVE',l)
      let query = obj || {}
      this.$router.push({
        path:l,
        query
      })
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ]),
    administratorInfo() {
      // 代管理 -- 管理员信息
      return this.$route.query.administratorInfo
    }
  },
}
</script>
